import React, { Component  } from 'react'
import "../../stylesheets/partials/common.scss"

class Loading extends Component {
  render(){
    const { completed } = this.props;
    return(
      <div className="loading" >{ completed ? '' : ''}</div>
    )
  }
}

export default Loading


