import React, {Component} from 'react';
import {IndexLink, Link} from 'react-router';
import {connect} from 'react-redux';
import '../../stylesheets/partials/modules/App.scss';
import {baseUrl} from "../api/config"
class App extends Component {
  constructor() {
    super();
  }

  render() {
    return (
      <div>
        <div ref="content">{ this.props.children }</div>
        <div className="footer">
          <ul>
            <li><IndexLink activeClassName='active' to={baseUrl}></IndexLink></li>
            <li><Link activeClassName='active' to={baseUrl+"tutor"}></Link></li>
            <li><Link activeClassName='active' to={baseUrl+"account"}></Link></li>
          </ul>
        </div>
      </div>
    )
  }
}
App = connect()(App);
export default App;


